import React, { useState,useEffect }  from 'react';
import { useDispatch,useSelector} from 'react-redux'
import { updateAlert, updateReset } from '../../../store/ssoSlice';
import { registerAlumni } from '../../../store/utils/ssoApi';
import { useHistory } from 'react-router';

const SSOSignupAlumni = () => {
	
	const dispatch = useDispatch()
	const sso = useSelector( state => state.sso)
	const { reset } = sso;
	const [ data,setData ] = useState({})
	const [ isLoading,setIsLoading ] = useState(false)
	const history = useHistory();

	const onChange = (e) => {
	  setData({...data, [e.target.name]:e.target.value})
	}

	const onSubmit = async(e) => {
	  e.preventDefault()
	  setIsLoading(true)
	  console.log(data);
	  if((data.pass && data.pass == data.rpass) || (data.is_staff && data.emp_no)){
		  console.log(data)
		const resp = await registerAlumni(data)
		if(resp.success){
		  dispatch(updateAlert({show:true,message:`ALUMNI ACCOUNT CREATED!`,type:'success'}))
		  setData({})
		  setIsLoading(false);
		  setTimeout(() => {
			history.push('/login')
		  },3000)
		
		}else{
		  dispatch(updateAlert({show:true,message:`${resp.msg.toUpperCase()}`,type:'error'}))
		  setIsLoading(false);
		}
	  }else{
		dispatch(updateAlert({show:true,message:`${data.is_staff ? 'PLEASE ENTER UCC STAFF NUMBER!':'PLEASE CHECK SSO PASSWORDS!'}`,type:'error'}))
		setIsLoading(false);
	  }
	}


	useEffect(() => {
	  setData({...reset})
	},[])

	useEffect(() => {
	  dispatch(updateReset({...data}))
	},[data])

    return (

      <form className="login-form__form" onSubmit={onSubmit}>
		 {/*<h3 className="login-form__title" style={{color:'#fff'}}>hh</h3>*/}
		 <div className="login-form__field ae-input__input">
			 <label className="text-white">TITLE</label>
			 <select name="title" name="title" onChange={onChange} value={data.title}>
			    <option value="Mr.">Mr.</option>
				<option value="Mrs.">Mrs.</option>
				<option value="Ms.">Ms.</option>
				<option value="Dr.">Dr.</option>
				<option value="Prof.">Prof.</option>
				<option value="Ing.">Ing.</option>
				<option value="Esq.">Esq.</option>
				<option value="Rev.">Rev.</option>
				<option value="Rev. Dr.">Rev. Dr.</option>
				<option value="Rev. Mrs.">Rev. Mrs.</option>
			</select>
		</div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="fname" onChange={onChange} value={data.fname} placeholder="FIRST NAME " className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required />
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="mname" onChange={onChange} value={data.mname} placeholder="MIDDLE NAME" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="lname" onChange={onChange} value={data.lname} placeholder="LAST NAME" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="dob" onChange={onChange} value={data.dob} onFocus={(e) => e.currentTarget.setAttribute('type','date')} onBlur={(e) => e.currentTarget.setAttribute('type','text')} placeholder="DATE OF BIRTH" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="phone" onChange={onChange} value={data.phone} placeholder="PRIMARY PHONE NUMBER" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="mobile" onChange={onChange} value={data.mobile} placeholder="ALTERNATIVE PHONE NUMBER" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="email" name="email" onChange={onChange} value={data.email} placeholder="PRIMARY E-MAIL ADDRESS" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="address" onChange={onChange} value={data.address} placeholder="PERSONAL ADDRESS" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			 <select name="idtype" onChange={onChange} value={data.idtype}>
			     <option>-- CHOOSE NATIONAL ID TYPE --</option>
			     <option value="NIA">GHANA CARD (NIA)</option>
				 <option value="VOTERS">VOTERS ID</option>
				 <option value="DRIVERS">DRIVERS LICENCE</option>
				 <option value="PASSPORT">INTERNATIONAL PASSPORT</option>
			 </select>
			 <input type="text" name="idvalue" onChange={onChange} value={data.idvalue} placeholder="NATIONAL IDENTIFICATION NUMBER" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
         </div><br/><br/>
		 <div className="login-form__field ae-input__input">
			 <select name="country_id" onChange={onChange} value={data.country_id}>
			     <option>-- CHOOSE RESIDENT COUNTRY --</option>
			     <option value="GH">GHANA</option>
				 <option value="TG">TOGO</option>
				 <option value="NG">NIGERIA</option>
			 </select>
			 <input type="text" name="location" onChange={onChange} value={data.location} placeholder="RESIDENT TOWN OR LOCATION" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
         </div><br/><br/>

		 <div className="login-form__field ae-input__input">
			<input type="text" name="qualification" onChange={onChange} value={data.qualification} placeholder="RECENT QUALIFICATION" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="occupation" onChange={onChange} value={data.occupation} placeholder="CURRENT OCCUPATION" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular" required/>
		 </div>
		<div className="login-form__field ae-input__input">
			<input type="text" name="employer_name" onChange={onChange} value={data.employer_name} placeholder="NAME OF EMPLOYER" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="employer_address" onChange={onChange} value={data.employer_address} placeholder="EMPLOYER ADDRESS" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="email" name="emp_mail" onChange={onChange} value={data.emp_mail} placeholder="EMPLOYEE EMAIL ADDRESS" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="text" name="emp_no" onChange={onChange} value={data.emp_no} placeholder="EMPLOYEE STAFF NUMBER" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			 <label className="text-white">ARE YOU AN ACTIVE UCC STAFF?</label>
			 <select name="is_staff" onChange={onChange} value={data.is_staff}>
			    <option value="1">YES</option>
				<option value="0" selected>NO</option>
			 </select>
		</div><hr className="login_hr" style={{ outline:'none' }}/>
		 <div className="login-form__field ae-input__input">
			<input type="password" name="pass" onChange={onChange} value={data.pass} placeholder="SET SSO ACCOUNT PASSWORD" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="password" name="rpass" onChange={onChange} value={data.rpass} placeholder="REPEAT SSO ACCOUNT PASSWORD" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <button type="submit" className="login-form__submit-button ae-button__green ae-button__button"><span className="ae-button__label"><b>{ isLoading ? 'PROCESSING ...' : 'SIGN UP'}</b></span></button>
	  </form>
	)
}

export default SSOSignupAlumni;
