import React, { useEffect,useState }  from 'react';
import { useSelector,useDispatch } from 'react-redux';
import { BrowserRouter as Router, Route,Switch,useHistory,Redirect } from 'react-router-dom';
import { withCookies } from "react-cookie";
import SSOLoginLayout from './mainui/pages/SSOLoginLayout';
import SSOReset from './mainui/pages/SSOReset';
import SSOPageLayout from './mainui/pages/SSOPageLayout';
import SSORoleLayout from './mainui/pages/SSORoleLayout';
import SSOModuleLayout from './mainui/pages/SSOModuleLayout';
import StudentLayout from './mainui/apps/students/layouts/StudentLayout';
import SSOSignup from './mainui/pages/SSOSignup';
import Favicon from 'react-favicon';

function App({cookies}) {
  const [ fav,setFav ] = useState('')
  	
  const { REACT_APP_THEME_TAG : themetag } = process.env; 
  const dispatch = useDispatch();
  const { setting } = useSelector(state => state.theme );
  const { user,isLoggedIn } = useSelector(state => state.sso );
  
  const themeLoader = (tag) => {
     const ui = setting.clients.find( r => r.tag === tag)
     /* const uccbg = getComputedStyle(document.documentElement).getPropertyValue('--uccbg'); */
     // Set Theme
     document.documentElement.style.setProperty('--uccbg', `${ui && ui.ui.uccbg}`);
     document.documentElement.style.setProperty('--ucctopbar', `${ui && ui.ui.ucctopbar}`);
     document.documentElement.style.setProperty('--uccmain', `${ui && ui.ui.uccmain}`);
     document.documentElement.style.setProperty('--uccmain-dark', `${ui && ui.ui.uccmaindark}`);
     document.documentElement.style.setProperty('--uccsec', `${ui && ui.ui.uccsec}`);
     document.documentElement.style.setProperty('--uccsec-dark', `${ui && ui.ui.uccmaindark}`);
     document.documentElement.style.setProperty('--uccmain-light', `${ui && ui.ui.uccmainlight}`);
     document.documentElement.style.setProperty('--uccaccent', `${ui && ui.ui.uccaccent}`);
     document.documentElement.style.setProperty('--uccaccent-dark', `${ui && ui.ui.uccaccentdark}`);
  }

  useEffect(() => {
    themeLoader(themetag); // Load Theme
    import(`./assets/img/logo/${themetag}/favicon/favicon.ico`).then(src => setFav(src.default))
	},[])
  
  return (
    <>
    <Favicon url={fav} />
    <Router>
      <Switch>
        <Route path="/ssoReset" component={SSOReset}/>
        <Route path="/signup" component={SSOSignup}/>
        <Route path="/dash" component={SSOPageLayout}/>
        <Route path="/student" component={StudentLayout}/>
        <Route exact={true} path="/app" component={SSORoleLayout}/>
        <Route path="/app/:module" component={SSOModuleLayout}/>
        <Route path="/login" component={SSOLoginLayout}/>
        <Route exact={true} path="/" render={ props => <Redirect to={{ pathname: "/login"}} /> }/>
      </Switch>
    </Router>
    </>
  );
}

export default withCookies(App);
