import React, { useState, useEffect }  from 'react';
import './SSOReset.css';
import './SSOLogin.css';
import Logo from '../../assets/img/logo.png'
import { useDispatch,useSelector} from 'react-redux'
import AlertBox from '../components/AlertBox';
import { updateReset } from '../../store/ssoSlice';
import SSOSignupAlumni from '../components/signup/SSOSignupAlumni';
import SSOSignupService from '../components/signup/SSOSignupService';
import SSOSignupJob from '../components/signup/SSOSignupJob';
import SSOSignupNss from '../components/signup/SSOSignupNss';
import { Link, useParams,useLocation, useHistory } from 'react-router-dom';


const SSOSignup = () => {
	 const dispatch = useDispatch()
	 const sso = useSelector( state => state.sso)
	 const { reset } = sso;
     const { setting } = useSelector( state => state.theme )
	 const ui = setting.clients.find( r => r.tag === setting.tag )
	 const history = useHistory();
	 
	 const useQuery = () => {
		return new URLSearchParams(useLocation().search);
 	 }
	 const query = useQuery();
	 const { module }  = useParams();
	 const action  = query.get('action');
	 
	 const goBack = (e) => {
		e.preventDefault()
        // Cancel Login Reset
		dispatch(updateReset({}))
        history.push('/')
	 }
	 
	 const showPage = () => {
		switch(action){
		   case 'alumni': return <SSOSignupAlumni/>; break;
		   case 'services': return <SSOSignupService/>; break;
		   case 'jobs': return <SSOSignupJob/>; break;
		   case 'nss': return <SSOSignupNss/>; break;
		   default: return <SSOSignupService/>; break;
	    }
	 }

	 const title = () => {
		switch(action){
		   case 'alumni': return 'ALUMNI REGISTRATION'; break;
		   case 'services': return 'UCC ONLINE SERVICES'; break;
		   case 'jobs': return 'JOB PORTAL SIGN-UP '; break;
		   case 'nss': return 'UCC NSS STAFF REGISTRATION'; break;
		   default: return 'UCC ALUMNI REGISTRATION'; break;
	    }
	 }
	 
	  
     return (
        <div id="main">
			<div className="layout">
				
				<div className="layout__content">
					<div className="layout-login">
						<div className="layout-login__inner">
							<div className="layout-login__form layout-login__reset">
								<div className="layout-login__form__inner">
									<div className="login-form__head"></div>
									<div className="login-form signin-form">
										<div className="login-form__title">{ title() }</div>
										<img src={Logo} className="login-icon"/>
										{/* Content */}
										{ showPage() }
										<Link className="login-form__link signin-form__forgot-password" onClick={ goBack } style={{float:'right',textAlign:'right',fontSize:'12px',fontWeight:'bolder',marginTop:'25px',color:'rgb(255, 238, 31)'}}><b>Cancel and Go back To Login !</b></Link>   
									</div>
									<AlertBox/>
								</div>
								<div className="footer">
									<div className="footer__inner">Copyright © {new Date().getFullYear()} {ui && ui.tag.toUpperCase()}<a href="#" style={{display:'none'}} target="_blank">Terms of Service</a></div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	
      )
}

export default SSOSignup;
