import React, { useState,useEffect }  from 'react';
import { useDispatch,useSelector} from 'react-redux'
import { updateAlert, updateReset } from '../../../store/ssoSlice';
import { sendPwd } from '../../../store/utils/ssoApi';
import { useHistory } from 'react-router';
import { passwordStrength } from 'check-password-strength'

const SSOResetForm = () => {
	
	const dispatch = useDispatch()
	const sso = useSelector( state => state.sso)
	const { reset } = sso;
	const [ data,setData ] = useState({})
	const [ isLoading,setIsLoading ] = useState(false)
	const [ pwdinfo,setPwdinfo ] = useState({value:''})
	const history = useHistory();
	const options = [
		{
		  id: 0,
		  value: "Too weak",
		  minDiversity: 0,
		  minLength: 0
		},
		{
		  id: 1,
		  value: "Weak",
		  minDiversity: 2,
		  minLength: 6
		},
		{
		  id: 2,
		  value: "Medium",
		  minDiversity: 3,
		  minLength: 8
		},
		{
		  id: 3,
		  value: "Strong",
		  minDiversity: 4,
		  minLength: 10
		}
	  ]

	const onChange = (e) => {
	  if(e.target.name == 'pass') setPwdinfo({value:''})
	  if(e.target.name == 'pass') setPwdinfo({value:''})
      setData({...data, [e.target.name]:e.target.value})
	}

	const checkPwd = (e) => {
	  const vs = e.target.value.trim()
	  const res = passwordStrength(vs,options)
	  if(res) setPwdinfo({...res})
	}

	const onSubmit = async(e) => {
	  e.preventDefault()
	  setIsLoading(true)
	  if(data.pass && data.pass == data.rpass){
		const resp = await sendPwd({ email:data.email,password:data.pass, sdata:data.sdata })
		if(resp.success){
		  dispatch(updateAlert({show:true,message:`PASSWORD CHANGED!`,type:'success'}))
		  setData({...data,view:'email',pass:'',rpass:'',token:'',email:'', mtoken:''})
		  setIsLoading(false);
		  setTimeout(() => {
			history.push('/login')
		  },3000)
		
		}else{
		  dispatch(updateAlert({show:true,message:`PROCESS FAILED!`,type:'error'}))
		  setIsLoading(false);
		}
	  }else{
		dispatch(updateAlert({show:true,message:`PLEASE CHECK PASSWORDS!`,type:'error'}))
		setIsLoading(false);
	  }
	}


	useEffect(() => {
	  setData({...reset})
	  console.log(reset)
	},[])

	useEffect(() => {
	  dispatch(updateReset({...data}))
	},[data])

    return (

      <form className="login-form__form" onSubmit={onSubmit}>
		 <h3 className="login-form__title" style={{color:'#567399', padding:'10px auto',border:'3px solid rgb(19 61 113)', borderRadius:'5px', fontSize:'9px'}}>{data.sdata && `NAME: ${data.sdata.user.fname} ${data.sdata.user.lname}`}</h3>
		 <h3 className="login-form__title" style={{color:'#567399', padding:'10px auto',border:'3px solid rgb(19 61 113)', borderRadius:'5px'}}>{data.email}</h3>
		 <h3 className="login-form__title" style={{color:'#ffee1f', padding:'10px auto',border:'3px dashed #ffee1f'}}>{data.tag}</h3>
		 
		 <div className="login-form__field ae-input__input">
			<input type="password" name="pass" onChange={onChange} onBlur={checkPwd} value={data.pass} placeholder="SET NEW PASSWORD" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
			{ [0,1].includes(pwdinfo.id) ? <b className="ae-input__error-msg ae-input__font-roboto-regular">{pwdinfo.value}</b> : null}
		 </div>
		 <div className="login-form__field ae-input__input">
			<input type="password" name="rpass" onChange={onChange} value={data.rpass} placeholder="REPEAT NEW PASSWORD" className="ae-input__native-dark ae-input__native ae-input__font-roboto-regular"/>
		 </div>
		 <button type="submit" disabled={[0,1].includes(pwdinfo.id) || ( data.pass && data.pass.length < 8 ) || !data.pass } className="login-form__submit-button ae-button__green ae-button__button"><span className="ae-button__label"><b>{ isLoading ? 'PROCESSING ...' : 'SAVE PASSWORD'}</b></span></button>
	  </form>
	)
}

export default SSOResetForm;
