import React, { useState, useEffect }  from 'react';
import './SSOReset.css';
import './SSOLogin.css';
import Logo from '../../assets/img/logo.png'
import SSOResetForm from '../components/reset/SSOResetForm';
import SSOResetToken from '../components/reset/SSOResetToken';
import { Link,useHistory } from 'react-router-dom';
import SSOResetEmail from '../components/reset/SSOResetEmail';
import { useDispatch,useSelector} from 'react-redux'
import AlertBox from '../components/AlertBox';
import { updateReset } from '../../store/ssoSlice';


const SSOReset = () => {
	 const dispatch = useDispatch()
	 const sso = useSelector( state => state.sso)
	 const { reset } = sso;
	 const { setting } = useSelector( state => state.theme )
	 const ui = setting.clients.find( r => r.tag === setting.tag )
     const history = useHistory();

	 const goBack = (e) => {
		e.preventDefault()
        // Cancel Login Reset
		dispatch(updateReset({...reset,view:'email',pass:'',rpass:'',token:'',email:'', mtoken:'',returnurl:''}))
        history.push('/')
	 }
	 
	 const showPage = () => {
		switch(reset && reset.view){
		   case 'token': return <SSOResetToken/>; break;
		   case 'form': return <SSOResetForm/>; break;
		   case 'email': return <SSOResetEmail/>; break;
		   default: return <SSOResetEmail/>; break;
	    }
	 }
	 
	  
     return (
        <div id="main">
			<div className="layout">
				
				<div className="layout__content">
					<div className="layout-login">
						<div className="layout-login__inner">
							<div className="layout-login__form layout-login__reset">
								<div className="layout-login__form__inner">
									<div className="login-form__head"></div>
									<div className="login-form signin-form">
										<div className="login-form__title">ACCOUNT RESET</div>
										<img src={Logo} className="login-icon"/>
										{/* Content */}
										{showPage()}
										<Link className="login-form__link signin-form__forgot-password" onClick={goBack} style={{float:'right',textAlign:'right',fontSize:'12px',fontWeight:'bolder',marginTop:'25px'}}><b>Cancel and Go back To Login !</b></Link>   
									</div>
									<AlertBox/>
								</div>
								<div className="footer">
									<div className="footer__inner">Copyright © {new Date().getFullYear()} {ui && ui.tag.toUpperCase()}.<a href="#" style={{display:'none'}} target="_blank">Terms of Service</a></div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	
      )
}

export default SSOReset;
